@import '../Util/variables.scss';
@import '../Util/selectors.scss';

.riemieeBox {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 80%;
  aspect-ratio: 16/13;
  transition: 2s;
  border: solid 1px black;
  border-radius: 10%;
  margin-right: 2em;
  margin-bottom: 5%;
}

.riemieePic {
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: 10%;
  z-index: -5;
}

.riemieeModal {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom:0;
  background-color: rgba(0, 0, 0, 0);
  transition: 1s;
  border-radius: 10%;
  padding: 1.5em;
}

.projMainBox {
  display: flex;
  justify-content: space-between;
  flex-direction: column;
  width: 100%;
  height: 100%;
}

.projName {
  font-family: $main-font;
  font-size: 3vw;
  font-weight: 500;
  color: $sptext-color;
}

.projDesc {
  font-family: $second-font;
  font-size: 2vw;
  font-weight: 500;
  color: $sptext-color;
}

.projTextBox {
  display: flex;
  justify-content: space-between;
  padding-bottom: .7em;
  align-items: flex-end;
}

.riemieeHover {
  background-color: rgba(0, 0, 0, .9);
}

.mainImg {
  position: relative;
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: 10%;
  opacity: 1;
}