.main {
  display: flex;
  flex-direction: column;
  height: 100vh;
  position: relative;
}

.box {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.proj {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  padding-top: 2%;
  width: 100%;
} 

.proj iframe {
  position: absolute;
  width: 52%;
  height: 69%;
  top: 13%;
  left: 24%;
  z-index: -2;
}

.proj img {
  width: 70%;
  object-fit: fill;
  object-position: -20% 20%;
  z-index: -3;
}